function List(props){

    let items = props.itemList.map((item, index)=>{
        return <div className="row justify-content-around">
            <span className="col-1">{index+1}.</span>
            <div className="col-9">{item.text}</div>
            <i className="bi bi-trash-fill col-1" onClock={props.deleteTodo}></i>
        </div>
    })

    // {items} - массив jsx-элементов

    let elem=
    <div>
        {items}
    </div>

    return elem
}

export default List