import React from "react"
import List from "./List"

class App extends React.Component {

  //функция которая настраивает React-Component
  constructor(props){
  
      super(props)

      this.state = {
        items: localStorage.getItem("items") != null ? JSON.parse(localStorage.getItem("items")) : [{"text": "Добавь дело!"}]        
      }
      this.addTodo = this.addTodo.bind(this)
      this.deleteTodo = this.deleteTodo.bind(this)

  }

  addTodo(e){

    e.preventDefault()

    let form = e.target
    let newTodo = form.todoInput.value

    this.setState( (state)=>{

      let newState = state;

      if(newTodo != ""){
        newState.items.push({text:newTodo})
      }

      localStorage.setItem("items", JSON.stringify(newState.items))
      
      return {newState} 
    });

    
    form.todoInput.value = ""

    
  //    ___
  //   (   )
  // ( |   |
  //   [___]



  }

  deleteTodo(e){
    let todoIndex = e.target.parentNode.childNodes[0].innerHTML - 1

    this.setState((state)=>{
      let newState = state

      let items = newState.items

      items.splice(todoIndex, 1)

      newState.items = items;
      
      localStorage.setItem("items", JSON.stringify(newState.items))
      
      return{newState}
    })


    
  }

  //рендер нашего компонента
  render(){
    let elem =
    <div id="todo-list" className="container-md">
      <div className="row justify-content-center">
        <div className="col-4">
          <form className="row" onSubmit={this.addTodo}>
            <input name="todoInput" type="text" className="col form-control" placeholder="Введите дело"/>
            <button type="submit" className="col-3 btn-danger">Добавить</button>
          </form>
        </div>
      </div>
      <div className="row justify-content-center">
          <div className="col-4">
            <List itemList={this.state.items} />
          </div>
      </div>
    </div>

  return elem

  }



}  
  
export default App